import React from "react";
import HeadlineWithFocus from "../components/HeadlineWithFocus";
import Obfuscate from "react-obfuscate";
import { FaEnvelopeO, FaTwitter, FaGithub, FaInstagram, FaLinkedin } from "react-icons/lib/fa/";
import Helmet from "react-helmet";

export default props => {
  return (
    <div className="c-content-box">
      <Helmet title="Contact me" />

      <HeadlineWithFocus>
        You can get in touch <span>anytime through</span>
      </HeadlineWithFocus>
      <ul className="c-contact-list">
        <li className="c-contact-list__item">
          <FaEnvelopeO />
          <Obfuscate email="ramitkaul@gmail.com" />
        </li>
        <li className="c-contact-list__item">
          <FaGithub />
          <a
            href="https://github.com/rkwap"
            target="_blank"
            rel="noopener noreferrer"
          >
            rkwap
          </a>
        </li>
        <li className="c-contact-list__item">
          <FaLinkedin />
          <a
            href="https://www.linkedin.com/in/ramitkoul/"
            target="_blank"
            rel="noopener noreferrer"
          >
            ramitkoul
          </a>
        </li>
        <li className="c-contact-list__item">
          <FaInstagram />
          <a
            href="https://instagram.com/ramitkoul"
            target="_blank"
            rel="noopener noreferrer"
          >
            @ramitkoul
          </a>
        </li>
      </ul>
    </div>
  );
};



